import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Task from '../Task/Task';
import { connect } from 'react-redux';

import { listTask } from "../../store/actions/tasks";

import firebase from "../../firebase/firebase";

class Tasks extends Component {
  
  constructor(){ 
    super(); 
    this.db_tasks = firebase.database().ref().child("users");
  }

  state = {
    tasks: []
  }

  componentDidMount(){
    let tasks = []
    this.db_tasks.on("child_added", snap => {
      if(snap.val() !== null){        
        tasks = [ 
          ...tasks,
          {
            id: snap.key,
            title: snap.val().name,
            description: snap.val().email,
          }
        ]
        this.setState({tasks})
      }      
    })  
    
    this.db_tasks.on("child_removed", snap => {
      if (snap.val() !== null) {
        tasks = tasks.filter(task => task.id !== snap.key)
        this.setState({tasks})
      }
    })

  }

  render(){
    
    const tasks = this.state.tasks.map( task =>
      <Task key={task.id} task={task} />
    )

    let list = <i>Empty list</i> 
    
    if(tasks.length)
      {list = <ul className="list-group">{tasks}</ul> }     

    return (
      <>
        <h4 className="mb-3">Messages</h4>
        {list}
      </>
    )
  }
}

Tasks.propTypes = {
  tasks: PropTypes.array.isRequired,
}

const mapStateToProps = state => {
  return {
    tasks: state.tasks.tasks
  }
}

const mapDispatchToProps = dispatch => {
  return {
    listTask(payload) {
      dispatch(listTask(payload))
    },
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(Tasks);
