import React, { Component } from 'react';
import firebase from "../../firebase/firebase";

class TaskForm extends Component {

  constructor() {
    super();
    this.db_tasks = firebase.database().ref().child("users");
  }

  state = {
    title: '',
    description: '',
  }

  onSubmit = e => {
    e.preventDefault()
    const payload = {
      name: this.state.title,
      email: this.state.description,
      age: 25
    }
    this.db_tasks.push(payload, function() {
      console.log("Data has been insert")            
    })
    this.setState({
      description: ''
    })
  }

  onChange = e => {
    const { name , value } = e.target;
    this.setState({
      [name]: value
    })  
  }

  render(){
    return (
      <form className="py-3" onSubmit={this.onSubmit}>
        <h5>Create a Message</h5>
        <div className="input-group">
          <input name="title" type="text" className="form-control" onChange={this.onChange} value={this.state.title} placeholder="Your name"/>
        </div>
        <div className="input-group">
          <textarea name="description" className="form-control" onChange={this.onChange} value={this.state.description} placeholder="Message textarea" aria-label="Message textarea"></textarea>
        </div> 
        <div className="input-group">
          <button type="submit" className="btn btn-success mt-3">Send message</button>
        </div>
      </form>
    )
  }
}

export default TaskForm;