import React, { Component } from 'react';
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { toggleTask, readTask } from "../../store/actions/tasks";

import firebase from "../../firebase/firebase";


class Task extends Component {
  constructor() {
    super();
    this.db_ref = firebase.database().ref()
  }
  
  removeTask = id => {
    const question = window.confirm("Are you sure delete it?")
    if(question)    
      this.db_ref.child("users/"+id).remove()
  }
  render(){


    const {task} = this.props;
    
    const doneStyles = {
      textDecoration: 'line-through',
      color: 'gray',
    }   

    
    return (
      <li className="list-group-item">
        {/* <div> */}
          {/* <input type="checkbox" checked={task.done} onChange={this.props.toggleTask.bind(this, task.id)} className="mr-2" /> */}
          <b style={task.done ? doneStyles : {}}>{task.title} say: </b>
          <button onClick={this.removeTask.bind(this, task.id)} className="btn btn-sm text-danger float-right">&times;</button>
        {/* </div>     */}
        <i>{task.description}</i>  
        {/* <button onClick={this.props.readTask.bind(this, task.id)} className="btn btn-sm text-info float-right">Edit</button> */}
      </li>
    )
  }
}
Task.propTypes = {
  task: PropTypes.object.isRequired,
}

const mapDispatchToProps = dispatch => {
  return {
    toggleTask(payload) {
      dispatch(toggleTask(payload))
    },
    readTask(payload) {
      dispatch(readTask(payload))
    },
  }
}

export default connect(null , mapDispatchToProps)(Task);